const addlist = (state=[],action)=>{
    switch(action.type){
        case 'ADDLIST':
            console.log(state.length);
            state.map((item)=>{
                if(action.value===item){
                    return state;
                }else{
                    return [...state,action.value]
                }
            })    
        default:
            return state;
        
    }
}

export default addlist;