import React from 'react'

class Order extends React.Component{
    render(){
        return (
            <div>
                <h2>这是订单列表页面</h2>
                <button
                style={{width:100,height:70,fontSize:30}} 
                onClick={()=>{
                    this.props.history.push('/home');
                }}>返回</button>
            </div>
        )
    }
   
}
export default Order
