

const dellistreducer = (state=[],action)=>{
    switch(action.type){
        case 'DELLIST':

        //获取state
            console.log("这是",state);
            console.log(action);
            var arr = [...state];
            
            
            arr.splice(action.index,1);
            return arr;   
        default:
            return state;
        
    }
}

export default dellistreducer;