import React from 'react'
import { connect } from 'react-redux';
import { withRouter } from 'react-router-dom';
import './shoucang.css'

const Shoucang = (props) => {
    
    return (
       console.log(props),
        <div>
           
            <div id='shoucangtitle'>
                <button className='back' onClick={()=>{
                    props.history.push('./home');
                }}>返回</button>
                <h1>收藏列表</h1>
            </div>
            
            <div className='shoucang'>
                {
                    props.dellist.map((item,idx) => (
                        <div key={idx}>
                            <img src={item}/>
                            <button className='delbtn'
                                onClick={()=>{
                                    console.log(props);
                                    console.log(idx)
                                    props.dispatch({
                                        type:'DELLIST',
                                        index:idx
                                    })
                                
                                }}
                            
                                >删除</button>
                        </div>
                    ))
                }
           
            </div>
          
        </div>

            
    )
}


const mapStateToProps = (state) => ({
    addlist: state.addlist,
    dellist: state.dellist
    
  })
  
  export default connect(mapStateToProps)(withRouter(Shoucang));




