import React, { useEffect } from 'react'
import { SearchBar, Button, WhiteSpace, WingBlank } from 'antd-mobile';
import './details.css'
//import {login} from './actionCreators'
import { connect } from 'react-redux'
import { addlist } from './addlistreducer'
import { withRouter } from 'react-router-dom';


const Details = (props) => {

    

    var src = props.location.src
    return (
        <div>
           

            <img src={src} />

            <div className='cancell' onClick={() => {
                props.history.push('/home');

            }}>
                <span className="cancelTab">{'<'}</span>
            </div>



            <div className='bot'>
                <div className="botImg">
                    <i style={{ color: 'red' }} className='iconfont icondianpu'></i>
                    <span>店铺</span>
                </div>
                <div className="botImg">
                    <i className='iconfont iconkefu'></i>
                    <span>客服</span>
                </div>

                <div className="botImg" >

                    

                    <i className='iconfont iconshoucang' id="like"
                        {
                            ...useEffect(() => {
                                var like = document.getElementById('like');
                                 props.list.map((item) => {
                                    if (src === item) {
                                        like.className = 'iconfont iconstar__easyico';
                                    }else{
                                        like.className = 'iconfont iconshoucang';
                                    }
                                 })
                            },  [])
                        }

                        onClick={(prop) => {
                            
                            if (props.info === '') {
                                console.log(1111)
                                props.history.push('/login')

                            } else {
                                (prop.target).className = 'iconfont iconstar__easyico'

                                props.dispatch({
                                    type: 'ADDLIST',
                                    value: src
                                })
                            }
                        }}></i>
                    <span>收藏</span>
                </div>

                <div className='botbtn' id='add'>加入购物车</div>
                <div className='botbtn' id='buy'>立即购买</div>
            </div>
        </div>

    );
}

const mapStateToProps = (state) => ({
    info: state.userinfo,
    list: state.dellist,

})
export default connect(mapStateToProps)(withRouter(Details));
