import { createStore, compose, combineReducers, applyMiddleware } from 'redux';
// combineReducers作用是用多个todoreducer,homereducer整合多个
//import todo from './todosreducer';
import userinfo from './homereducer';
import datalist from './showreducer';
import addlist from './addlistreducer'
import dellist from './dellistreducer'
// import thunk from 'redux-thunk'

let rootReducer = combineReducers({
   datalist,
   userinfo,
   addlist,
   dellist
})


function logger({ getState }) {
    return (next) => (action) => {
        if(action.type==='ADDLIST'){
            getState().addlist.push(action.value);
            getState().dellist.push(action.value)
        }
        console.log('---------', action)
        console.log('state after dispatch', getState())
        return next(action)
    }
}

const thunk = ({ dispatch, getState }) => (next) => (action) => {

    if (typeof action === 'function') {
        return action(dispatch, getState);
    }

    return next(action);
};

const store = createStore(
    rootReducer,
    compose(
        
        applyMiddleware(thunk,logger),
        window.__REDUX_DEVTOOLS_EXTENSION__ && window.__REDUX_DEVTOOLS_EXTENSION__()
    )
);

export default store;
