import React, { Component } from 'react'
import { SearchBar, Button, WhiteSpace, WingBlank, Carousel } from 'antd-mobile';
import './home.css'
import Mytab from "./Mytab"
import {showlist} from './actionCreators'
import { connect } from 'react-redux'
import store from './store'
import { withRouter } from 'react-router-dom';

// {

//   datalist:[],

//   shoucang:[],

//   userinfo:'',

// }

class Home extends React.Component {

  state = {
    value: '',
    data: ['1', '2', '3'],
    imgHeight: 176,
    
  }

  componentDidMount() {
   
    setTimeout(() => {
      this.setState({
        value: '',
        data: ['140/O1CN01i5HblQ1CuA4mpYz96_!!140',
          '141/O1CN01UGVSzV1CucTWZoxCY_!!141',
          '1/O1CN01TgPhpb1BsV1d0nG8U_!!1'],
      });
    }, 1000);
    this.props.dispatch(showlist())
  }


  render() {
   var list = this.props.list;
    return (
      <div>
        <input className='inp' type="search" placeholder="寻找宝贝店铺" onClick={(props) => {
          this.props.history.push('/search');
        }}/>

        {/* 轮播图 */}
        <div className='lunbo'>
          <WingBlank>
            <Carousel className="space-carousel"
              autoplay
              infinite
              afterChange={index => this.setState({ slideIndex: index })}
            >
              {this.state.data.map((val, index) => (

                <img
                  src={`http://gw.alicdn.com/imgextra/i1/${val}-0-lubanu.jpg`}
                  alt=""
                  style={{ width: '100%' }}
                  onLoad={() => {
                    // fire window resize event to change height
                    window.dispatchEvent(new Event('resize'));
                    this.setState({ imgHeight: 'auto' });
                  }}
                />

              ))}
            </Carousel>
          </WingBlank>
        </div>

        {/* 商品列表 */}
        <div className='shopList'>        
          <div className='shopContext'>--❤你可能还喜欢--</div>
          <div className='shopImg'>
            {list.map((item)=>(
              <div className='Img' 
                onClick={(index)=>{
                    console.log(item);
                    this.props.history.push({pathname:'/details',src:item.img})
                }}
              
              >
                <img src={item.img}/>
                <div className='imgTxt'>
                  <span className='title'>{item.title}</span>
                </div>
                <div className='shopMoney'>
                  <span className='money'>￥ {item.price}</span>
                </div>
              </div>
               
              ))}
          </div>         
        </div>
        <Mytab />
      </div>
    )
  }

}



const mapStateToProps = (state) => ({
  list: state.datalist
  
  
})

export default connect(mapStateToProps)(withRouter(Home));
